body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
}

main div {
  padding: 15px;
  max-width: 125px;
  margin: 5px;
}

.header-region-content{
  max-width: 370px;
}

.div-with-bg-img{
  background-image: url('./images/img-neymar.png');
  background-size: 100% 100%;
  background-repeat: no-repeat;
  padding: 100px;
  padding-right: 300px;
  padding-bottom: 200px;
}

.share-region{
  padding: 50px;
}

.group-list{
  padding: 50px;
}

.padding-50{
  padding: 50px;
}

.img-chuteira{
  size: 100%;
}

.flex-center {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.how-works-content{
  padding-left: 80px;
  padding-top: 100px;
  padding-bottom: 100px;
  max-width: 450px;
}

.how-works-content p{
  padding-top: 0.1em;
}

.display-5{
  font-size: 2.4em;
}

.display-6{
  font-size: 2em;
}

.font-weight-lighter{
  font-weight: lighter;
}

.highlight-col{
  font-weight: bold;
}



